import React from 'react';
import './Home.css';
import { MapContainer, TileLayer, Marker, Popup } from 'react-leaflet';
import { LatLngBounds } from 'leaflet';
import Chart from './Chart/Chart';
import { useState, useEffect } from 'react';
import { getLatestLocations, getAllLocations, createLocation } from './WorldMapApi';



const Home = () => {
  const [markers, setMarkers] = useState([]);

  useEffect(() => {
    getLatestLocations()
      .then(response => {
        console.log(response.data.locations); // Gelen veriyi kontrol edelim.
        setMarkers(response.data);
      })
      .catch(err => console.error(err));
  }, []); // Bu useEffect hooku bileen yklendiinde bir kez alr.
  
  const maxBounds = new LatLngBounds(
    [-90, -180], // Gneybat ke koordinatlar
    [90, 180]    // Kuzeydou ke koordinatlar
    
  );
  return (
    <div className="home-container">
      {/* Filtreleme */}
      <div className="filters">
        <input type="date" className="start-date" />
        <input type="date" className="end-date" />
        <button className="filter-button">Filtrele</button>
      </div>

      {/* Kartlar */}
      <div className="cards">
        <div className="card">
          <div className="icon-container">
            {/* kon buraya eklenecek */}
          </div>
          <div className="card-info">
            <span className="number">150</span>
            <p className="title">Toplam  Says</p>
          </div>
        </div>
        <div className="card">
          <div className="icon-container">
            {/* kon buraya eklenecek */}
          </div>
          <div className="card-info">
            <span className="number">75</span>
            <p className="title">Ak ler</p>
          </div>
        </div>
        <div className="card">
          <div className="icon-container">
            {/* kon buraya eklenecek */}
          </div>
          <div className="card-info">
            <span className="number">30</span>
            <p className="title">XXX ler</p>
          </div>
        </div>
        <div className="card">
          <div className="icon-container">
            {/* kon buraya eklenecek */}
          </div>
          <div className="card-info">
            <span className="number">40</span>
            <p className="title">Bitirilen ler</p>
          </div>
        </div>
        <div className="card">
          <div className="icon-container">
            {/* kon buraya eklenecek */}
          </div>
          <div className="card-info">
            <span className="number">5</span>
            <p className="title">ptal Edilen ler</p>
          </div>
        </div>
        <div className="card">
          <div className="icon-container">
            {/* kon buraya eklenecek */}
          </div>
          <div className="card-info">
            <span className="number">20</span>
            <p className="title">Zaman Geen ler</p>
          </div>
        </div>
      </div>

      <div className="world-map">
      <MapContainer
          center={[51.505, -0.09]} // Balang koordinatlar
          zoom={1} // Balang yaknlatrma seviyesi
          minZoom={2} // Minimum yaknlatrma seviyesi
          maxZoom={17} // Maksimum yaknlatrma seviyesi
          maxBounds={maxBounds} // Harita snrlarn ayarlayn
          maxBoundsViscosity={1} // Harita snrlarna yapkanlk (1: tam snrlama)
      >
        <TileLayer
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
        />
        {markers.map((marker, index) => (
  <Marker key={index} position={[marker.atitude, marker.longitude]}>
    <Popup>
      <div>
        <strong>{marker.name}</strong> <br />
        <small>{new Date(parseInt(marker.latesttimestamp)).toLocaleString()}</small>
      </div>
    </Popup>
  </Marker>
))}


      </MapContainer>
    </div>
      {/* statistikler */}
      <div className="statistics">
        <div className="weekly-statistics">
      <Chart />
       </div>
        <div className="general-statistics">
          <h3>Genel statistik</h3>
          {/* statistik bileeni buraya eklenecek */}
        </div>
      </div>

      {/*  zerinde alan Kullanclar Tablosu */}
      <div className="users-working">
        <table className="users-table">
          <thead>
            <tr>
              <th>Kullanc</th>
              <th>alt </th>
              <th>Al Zaman</th>
              <th>Balama Zaman</th>
              <th>Son Dzenlenme Tarihi</th>
              <th>Durumu</th>
              <th>#</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>Ahmet Ylmaz</td>
              <td>Proje X</td>
              <td>2023-04-01</td>
              <td>2023-04-02</td>
              <td>2023-04-08</td>
              <td>Aktif</td>
              <td>1</td>
            </tr>
            <tr>
              <td>Fatma Demir</td>
              <td>Proje Y</td>
              <td>2023-04-03</td>
              <td>2023-04-04</td>
              <td>2023-04-09</td>
              <td>Aktif</td>
              <td>2</td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default Home;