import React, { useState } from 'react';
import './Chart.css';

const Line = ({ x1, y1, x2, y2, color }) => (
  <line x1={x1} y1={y1} x2={x2} y2={y2} stroke={color} strokeWidth="2" />
);

const Circle = ({ cx, cy, r, fill, onMouseEnter, onMouseLeave }) => (
  <circle cx={cx} cy={cy} r={r} fill={fill} onMouseEnter={onMouseEnter} onMouseLeave={onMouseLeave} />
);

const Chart = () => {
  const data = [
    { name: 'Pazartesi', i: 150, iptalEdilen: 50 },
    { name: 'Sal', i: 200, iptalEdilen: 50 },
    { name: 'aramba', i: 100, iptalEdilen: 50 },
    { name: 'Perembe', i: 250, iptalEdilen: 50 },
    { name: 'Cuma', i: 175, iptalEdilen: 50 },
    { name: 'Cumartesi', i: 50, iptalEdilen: 50 },
    { name: 'Pazar', i: 75, iptalEdilen: 50 },
  ];

  const [hoverData, setHoverData] = useState(null);

  const width = 600;
  const height = 300;
  const margin = { top: 20, right: 20, bottom: 50, left: 60 };
  const chartWidth = width - margin.left - margin.right;
  const chartHeight = height - margin.top - margin.bottom;

  const maxValue = Math.max(...data.map(d => Math.max(d.i, d.iptalEdilen)));
  const scaleY = value => (chartHeight * (1 - value / maxValue)) + margin.top;
  const scaleX = (value, length) => (chartWidth * (value / length)) + margin.left;

  return (
    <div className="chart" style={{ position: 'relative' }}>
      <svg width={width} height={height}>
        {data.map((d, i) => {
          const x1 = scaleX(i, data.length);
          const x2 = scaleX(i + 1, data.length);
          const y1 = scaleY(d.i);
          const y1ptal = scaleY(d.iptalEdilen);

          if (i < data.length - 1) {
            const y2 = scaleY(data[i + 1].i);
            const y2ptal = scaleY(data[i + 1].iptalEdilen);

            return (
              <g key={i}>
                <Line x1={x1} y1={y1} x2={x2} y2={y2} color="blue" />
                <Line x1={x1} y1={y1ptal} x2={x2} y2={y2ptal} color="red" />
              </g>
            );
          }

          return null;
        })}

        {data.map((d, i) => {
          const x = scaleX(i, data.length);
          const y = scaleY(d.i);
          const yptal = scaleY(d.iptalEdilen);

          return (
            <g key={i}>
              <Circle
                cx={x}
                cy={y}
                r={4}
                fill="blue"
                onMouseEnter={() =>
                  setHoverData({ x, y: y, label: '', value: d.i })
                }
                onMouseLeave={() => setHoverData(null)}
              />
              <Circle
                cx={x}
                cy={yptal}
                r={4}
                fill="red"
                onMouseEnter={() =>
                  setHoverData({
                    x,
                    y: yptal,
                    label: 'ptal Edilen ',
                    value: d.iptalEdilen,
                  })
                }
                onMouseLeave={() => setHoverData(null)}
              />
            </g>
          );
        })}

        {Array.from({ length: 5 }).map((_, i) => {
          const value = maxValue * (i + 1) / 5;
          const y = scaleY(value);
          return (
            <text key={i} x={margin.left - 15} y={y} textAnchor="end" dominantBaseline="central">
              {Math.round(value)}
            </text>
          );
        })}

        {data.map((d, i) => {
          const x = scaleX(i, data.length);
          return (
            <text key={i} x={x} y={height - margin.bottom + 20} textAnchor="middle" dominantBaseline="hanging">
              {d.name}
            </text>
          );
        })}
      </svg>

      {hoverData && (
        <div
          className="chart-tooltip"
          style={{
            left: hoverData.x + 10,
            top: hoverData.y - 25,
          }}
        >
          <div>{hoverData.label}</div>
          <div>Deer: {hoverData.value}</div>
        </div>
      )}
    </div>
  );
};

export default Chart;
