const API_URL = "https://portal-test.yenaengineering.nl/api";

export const createUser = async (userData) => {
    try {
      const response = await fetch(`${API_URL}/create_user`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(userData),
      });
  
      if (!response.ok) {
        throw new Error(`Kullanc oluturma hatas: ${response.statusText}`);
      }
  
      const data = await response.json();
      // console.log('Kullanc baaryla oluturuldu:', data);
      return data;
    } catch (error) {
      // console.error('Kullanc oluturulamad:', error);
      throw error;
    }
  };

export const getUsers = async () => {
    const response = await fetch(`${API_URL}/allusers`);
  
    if (!response.ok) {
      throw new Error(`Error fetching users: ${response.statusText}`);
    }
  
    return await response.json();
  };